<template>
  <div id="app">
    <div v-if = "showError === false" class="app-wrap">
      <div class="header" v-show="showHeader">
        <page-header></page-header>
      </div>
      <div class="content">
        <router-view/>
      </div>
      <div class="nav" v-show="showBottom">
        <page-nav></page-nav>
      </div>
    </div>
    <div v-else>
      <p class="page-error">你当前没有权限，请联系管理员</p>
    </div>
    <div class="mask" v-if="showMask" @click="hideMenu"></div>
    <date-select-panel></date-select-panel>
  </div>
</template>

<script>
import PageHeader from './components/common/pageHeader.vue'
import PageNav from './components/common/pageNav.vue'
import DateSelectPanel from './components/common/dateSelectPanel'
import { mapState } from 'vuex'
import WaterMark from 'js-watermark'
export default {
  name: 'App',
  data () {
    return {
    }
  },
  components: {
    PageHeader,
    PageNav,
    DateSelectPanel
  },
  computed: {// eslint-disable-next-line
    ...mapState ({
      showHeader: state => state.showHeader,
      showBottom: state => state.showBottom,
      showError: state => state.showError,
      showMask: state => state.showMask
    })
  },
  methods: {
    hideMenu () {
      this.$store.commit('updateShowLeft', false)
      this.$store.commit('updateShowMask', false)
    }
  },
  created () {
    WaterMark.configure({
      user: window.userAccount,
      company: window.userName,
      time: false,
      watermark_rows: 6,
      watermark_x_space: 20,
      watermark_y_space: 150,
      watermark_fontsize: '0.30333rem',
      watermark_color: '#fff', // 水印字体颜色
      watermark_alpha: 0.095 // 水印透明度
    })
  }
}
</script>

