<template>
  <ul class="page-nav">
    <li class="nav-item" v-for="(menu, index) of menuList" :key="index" @click="changeNav(index)">
        <router-link  :to="{path: menu.link}">
        <div class="icon">
          <img v-if="menu.incon && activeMenuIndex !== index"
            :src="menu.incon">
           <img v-if="menu.inconActive && activeMenuIndex === index"
            :src="menu.inconActive">
        </div>
      </router-link>
    </li>
  </ul>
</template>

<script>
export default {
  name: 'PageNav',
  data () {
    return {
      activeMenuIndex: 0
    }
  },
  computed: {
    userIdentity () {
      return this.$store.state.userIdentity
    },
    menuList () {
      const list = [
        {
          reportName: '总览',
          incon: './images/home@2x.png',
          inconActive: './images/homeActive@2x.png',
          link: '/home'
        },
        {
          reportName: '销售',
          incon: './images/sale@2x.png',
          inconActive: './images/saleActive@2x.png',
          link: '/sale'
        },
        {
          reportName: '回款',
          incon: './images/payback@2x.png',
          inconActive: './images/paybackActive@2x.png',
          link: '/payback'
        },
        {
          reportName: '毛利',
          incon: './images/gain@2x.png',
          inconActive: './images/gainActive@2x.png',
          link: '/gain'
        },
        {
          reportName: '库存',
          incon: './images/stock@2x.png',
          inconActive: './images/stockActive@2x.png',
          link: '/stock'
        }
      ]
      if (this.userIdentity === '1') {
        list[0].link = '/home'
        list[1].link = '/sale'
        list[2].link = '/payback'
        list[3].link = '/gain'
        list[4].link = '/stock'
      } else if (this.userIdentity === '2') {
        list[0].link = '/home_company'
        list[1].link = '/sale_company'
        list[2].link = '/payback_company'
        list[3].link = '/gain_company'
        list[4].link = '/stock_company'
      } else if (this.userIdentity === '3') {
        list[0].link = '/home_shop'
        list[1].link = '/sale_shop'
        list[2].link = '/payback_shop'
        list[3].link = '/gain_shop'
        list[4].link = '/stock_shop'
      }
      return list
    }
  },
  methods: {
    changeNav (index) {
      this.activeMenuIndex = index
    }
  },
  created () {
    this.$eventBus.$on('resetActiveMenuIndex', () => {
      this.activeMenuIndex = 0
    })
  }
}
</script>

<style lang="scss" scoped>
  .page-nav {
    width: 100%;
    height: 100%;
    background-color: #061046;
    display: flex;
    .nav-item {
      flex: auto;
      display: flex;
      align-items: center;
      flex-direction: column;
      box-direction: normal;
      -webkit-box-orient: vertical;
      justify-content: space-around;
      font-size: px2rem(24);
      color: #60699E;
      .icon {
        justify-content: center;
        img {
          height: px2rem(76);
          width: px2rem(76);
        }
      }
    }
  }
</style>
