  getDataFromInterface (url, params = {}, options) {
    const configs = { ...axiosConfig, url, data: params, ...options }
    return new Promise((resolve, reject) => {
      axios(configs)
        .then(res => {
          resolve(res.data)
        })
        .catch(err => {
          reject(err)
        })
    })
  }
