import Vue from 'vue'
import Router from 'vue-router'
// // 总监层页面
// import Home from './views/home/home'
// import Sale from './views/sale/sale'
// import Payback from './views/payback/payback'
// import Gain from './views/gain/gain'
// import Stock from './views/stock/stock'
// // 子公司层页面
// import HomeCompany from './views_company/home/home'
// import SaleCompany from './views_company/sale/sale'
// import PaybackCompany from './views_company/payback/payback'
// import GainCompany from './views_company/gain/gain'
// import StockCompany from './views_company/stock/stock'
// // 店铺层页面
// import HomeShop from './views_shop/home/home'
// import SaleShop from './views_shop/sale/sale'
// import PaybackShop from './views_shop/payback/payback'
// import GainShop from './views_shop/gain/gain'
// import StockShop from './views_shop/stock/stock'

Vue.use(Router)

function switchPath () {
  let userIdentity = router.app.$options.store.state.userIdentity
  userIdentity = userIdentity === '' ? window.userIdentity : userIdentity
  if (userIdentity === '1') {
    return '/home'
  } else if (userIdentity === '2') {
    return '/home_company'
  } else if (userIdentity === '3') {
    return '/home_shop'
  }
}
const router = new Router({
  routes: [{
    path: '/',
    name: '/',
    redirect () {
      return switchPath()
    }
  },
  {
    path: '/home',
    name: 'Home',
    component: resolve => require(['./views/home/home'], resolve)
  },
  {
    path: '/sale',
    name: 'Sale',
    component: resolve => require(['./views/sale/sale'], resolve)
  },
  {
    path: '/payback',
    name: 'Payback',
    component: resolve => require(['./views/payback/payback'], resolve)
  },
  {
    path: '/gain',
    name: 'Gain',
    component: resolve => require(['./views/gain/gain'], resolve)
  },
  {
    path: '/stock',
    name: 'Stock',
    component: resolve => require(['./views/stock/stock'], resolve)
  },
  // 这里放子公司层页面
  {
    path: '/home_company',
    name: 'Home_company',
    component: resolve => require(['./views_company/home/home'], resolve)
  },
  {
    path: '/sale_company',
    name: 'Sale_company',
    component: resolve => require(['./views_company/sale/sale'], resolve)
  },
  {
    path: '/payback_company',
    name: 'Payback_company',
    component: resolve => require(['./views_company/payback/payback'], resolve)
  },
  {
    path: '/gain_company',
    name: 'Gain_company',
    component: resolve => require(['./views_company/gain/gain'], resolve)
  },
  {
    path: '/stock_company',
    name: 'Stock_company',
    component: resolve => require(['./views_company/stock/stock'], resolve)
  },
  // 这里放店铺层页面
  {
    path: '/home_shop',
    name: 'Home_shop',
    component: resolve => require(['./views_shop/home/home'], resolve)
  },
  {
    path: '/sale_shop',
    name: 'Sale_shop',
    component: resolve => require(['./views_shop/sale/sale'], resolve)
  },
  {
    path: '/payback_shop',
    name: 'Payback_shop',
    component: resolve => require(['./views_shop/payback/payback'], resolve)
  },
  {
    path: '/gain_shop',
    name: 'Gain_shop',
    component: resolve => require(['./views_shop/gain/gain'], resolve)
  },
  {
    path: '/stock_shop',
    name: 'Stock_shop',
    component: resolve => require(['./views_shop/stock/stock'], resolve)
  }
  ]
})
export default router
