<template>
  <div class="select-data-panel" v-if="showLeft">
    <div class="select-panel-title">
      {{searchPanelTitle}}
    </div>
    <div v-if="isShowDatePanel" class="select-panel-content">
      <div class="label-content">请选择年份：</div>
      <div class="btnList-content">
        <div class="item-content" v-for="(item, index) in  yearList" :key="'y' + index" @click="selectedOneYear(item, index)">
          <div class="item-name">{{item.name}}</div>
          <span :class="[activeYearIndex === index ? 'triangle-bottom' : '']"></span>
        </div>
      </div>
      <div class="label-content">请选择月份：</div>
      <div class="btnList-content btnList-content-month">
        <div class="item-content" v-for="(item, index) in  monthList" :key="'m' + index" @click="selectedOneMonth(item, index)">
          <div class="item-name">{{item.name}}</div>
          <span :class="[activeMonthIndex === index ? 'triangle-bottom' : '']"></span>
        </div>
      </div>
      <div class="label-content">请选择日期：</div>
      <div class="btnList-content btnList-content-day">
        <div :class="['item-content', item.name === '占位' ? 'item-null' : '']" v-for="(item, index) in  dayList" :key="'m' + index" @click="selectedOneDay(item, index)">
          <div v-if="item.name !== '占位'" :class="['item-name']">{{item.name}}</div>
          <span v-if="item.name !== '占位'" :class="[activeDayIndex === index ? 'triangle-bottom' : '']"></span>
        </div>
      </div>
      <div v-if="!isShopHomePage && showDwPanel" class="label-content"><span>{{dwName}}:</span><div class="search-btn" @click="showSearchPanel">切换</div></div>
      <div v-if="!isShopHomePage && showDwPanel" class="btnList-content-dqdw">
        <!--<div class="item-content-big" v-for="(item, index) in  orgList" :key="'dw' + index" @click="selectedOneOrg(item, index)">-->
          <!--<div class="item-name-big">{{item.name}}</div>-->
          <!--<span :class="[activeOrgIndex === index ? 'triangle-bottom-big' : '']"></span>-->
        <!--</div>-->
        <div class="item-content-big">
          <div class="item-name-big">{{selectedOrgNameInit}}</div>
          <span :class="['triangle-bottom-big']"></span>
        </div>
      </div>
      <div v-if="isShopHomePage" class="label-content">请选择店铺：</div>
      <div v-if="isShopHomePage" class="selected-shops-btn" @click="showSelectShopPanel">勾选店铺</div>
      <div v-if="showUserTypeSwitch" class="label-content">身份切换 - 切换为{{userTypeLabel}}：</div>
      <div v-if="showUserTypeSwitch" class="btnList-content">
        <div :class="['item-content', item.name === '占位' ? 'item-null' : '']" v-for="(item, index) in  userTypeList" :key="'u' + index" @click="selectedUserType(item, index)">
          <div v-if="item.name !== '占位'" :class="['item-name']">{{item.name}}</div>
          <span v-if="item.name !== '占位'" :class="[activeUserTypeIndex === index ? 'triangle-bottom' : '']"></span>
        </div>
      </div>
    </div>
    <div v-if="isShowDatePanel" class="btn-content">
      <div class="button-cancel" @click="hideMenu">取消</div>
      <div class="button-confirm" @click="comfirmBtn">确定</div>
    </div>
    <div v-if="isShowSearchPanel" class="select-panel-content-search">
      <div class="label-content">{{searchPanelLabel}}</div>
      <div class="search-input">
        <van-search
          v-model="searchValue"
          placeholder="请输入搜索关键词"
        />
      </div>
      <div class="btnList-content-searchdw">
        <div class="item-content-big" v-for="(item, index) in  searchList" :key="'dw' + index" @click="selectedOneOrg(item, index)">
          <div class="item-name-big">{{item.name}}</div>
          <span :class="[activeOrgIndex === index ? 'triangle-bottom-big' : '']"></span>
        </div>
      </div>
    </div>
    <div v-if="isShowSearchPanel" class="btn-content">
      <div class="button-cancel" @click="hideMenu">取消</div>
      <div class="button-confirm" @click="comfirmBtn">确定</div>
    </div>
    <div v-if="isShowSelectShopPanel" class="select-panel-content-search">
      <div class="btns-content">
        <div class="btns" @click="selectAllShops(1)">
          <div class="item-name">全选</div>
          <span :class="[allShopSelected === 1 ? 'triangle-bottom' : '']"></span>
        </div>
        <div class="btns">
          <div class="item-name" @click="selectAllShops(0)">全不选</div>
          <span :class="[allShopSelected === 0 ? 'triangle-bottom' : '']"></span>
        </div>
      </div>
      <!--<div class="search-input">-->
        <!--<van-search-->
          <!--v-model="searchValue"-->
          <!--placeholder="请输入搜索关键词"-->
        <!--/>-->
      <!--</div>-->
      <div class="btnList-content-searchdw">
        <div class="item-content-big" v-for="(item, index) in selectedShopsList" :key="'shop' + index" @click="selectedOneShop(item, index)">
          <div class="item-name-big">{{item.name}}</div>
          <span :class="[item.selected ? 'triangle-bottom-big' : '']"></span>
        </div>
      </div>
    </div>
    <div v-if="isShowSelectShopPanel" class="btn-content">
      <!--<div class="button-cancel" @click="hideMenu">取消</div>-->
      <div class="button-confirm-all" @click="comfirmBtn">确定</div>
    </div>
  </div>
</template>

<script>
export default {
  name: '',
  data () {
    return {
      activeYearIndex: 0,
      activeMonthIndex: 0,
      activeDayIndex: 0,
      selectedYear: '2019',
      selectedMonth: '1月',
      selectedDay: '',
      selectedDate: '20190717',
      activeOrgIndex: 0,
      selectedOrgNameInit: '',
      selectedOrgName: '',
      userTypeList: [{id: '1', name: '否'}, {id: '2', name: '是'}],
      activeUserTypeIndex: 0,
      searchValue: '',
      isShowDatePanel: true,
      isShowSearchPanel: false,
      isShopHomePage: false,
      isShowSelectShopPanel: false,
      selectedShopsList: [],
      allShopSelected: 1
    }
  },
  computed: {
    dateStr () {
      return this.$store.state.dateStr
    },
    orgList () {
      let orgList = []
      if (this.userIdentity === '1' || this.userIdentity === '2') {
        orgList = this.$store.state.dwList
      } else if (this.userIdentity === '3') {
        orgList = this.$store.state.dpList
      }
      return orgList
    },
    showDwPanel () {
      if (this.userIdentity === '2' || this.userIdentity === '3') {
        return true
      } else {
        return false
      }
    },
    showUserTypeSwitch () {
      if (this.userIdentityInit === '1' || this.userIdentityInit === '2') {
        return true
      } else {
        return false
      }
    },
    dwName () {
      let str = ''
      if (this.userIdentity === '2') {
        str = '当前子公司'
      } else if (this.userIdentity === '3') {
        str = '当前店铺'
      }
      return str
    },
    showLeft () {
      return this.$store.state.showLeft
    },
    yearList () {
      const nowDate = new Date()
      const year = nowDate.getFullYear()
      const yList = []
      for (let i = year; i > year - 9; i--) {
        const y = {}
        y.name = i
        yList.push(y)
      }
      return yList
    },
    monthList () {
      const list = []
      for (let i = 1; i <= 12; i++) {
        const obj = {}
        obj.name = i + '月'
        list.push(obj)
      }
      return list
    },
    dayList () {
      const monthNum = this.selectedMonth.substring(0, this.selectedMonth.length - 1)
      let dayNum = 30
      if (parseInt(monthNum) === 1 || parseInt(monthNum) === 3 ||
        parseInt(monthNum) === 5 || parseInt(monthNum) === 7 || parseInt(monthNum) === 8 ||
        parseInt(monthNum) === 10 || parseInt(monthNum) === 12) {
        dayNum = 31
      }
      if (parseInt(monthNum) === 2) {
        const cond1 = this.selectedYear % 4 === 0 // 条件1：年份必须要能被4整除
        const cond2 = this.selectedYear % 100 !== 0 // 条件2：年份不能是整百数
        const cond3 = this.selectedYear % 400 === 0 // 条件3：年份是400的倍数
        // 当条件1和条件2同时成立时，就肯定是闰年，所以条件1和条件2之间为“与”的关系。
        // 如果条件1和条件2不能同时成立，但如果条件3能成立，则仍然是闰年。所以条件3与前2项为“或”的关系。
        // 所以得出判断闰年的表达式：
        if ((cond1 && cond2) || cond3) {
          dayNum = 29
        } else {
          dayNum = 28
        }
      }
      const list = []
      for (let i = 1; i <= dayNum; i++) {
        const obj = {}
        obj.name = i + '日'
        list.push(obj)
      }
      if (dayNum === 28) {
        list.push({name: '占位'})
        list.push({name: '占位'})
      }
      if (dayNum === 29) {
        list.push({name: '占位'})
      }
      if (dayNum === 31) {
        list.push({name: '占位'})
        list.push({name: '占位'})
      }
      return list
    },
    userTypeLabel () {
      let str = ''
      if (window.userIdentity === '1') {
        str = '子公司'
      } else if (window.userIdentity === '2') {
        str = '店铺'
      }
      return str
    },
    userIdentity () {
      return this.$store.state.userIdentity
    },
    isChangeUserType () {
      return this.$store.state.isChangeUserType
    },
    userIdentityInit () {
      return this.$store.state.userIdentityInit
    },
    selectedDpName () {
      return this.$store.state.selectedDpName
    },
    selectedDwName () {
      return this.$store.state.selectedDwName
    },
    searchPanelTitle () {
      let str = ''
      if (this.isShowSearchPanel) {
        str = '搜索'
      } else {
        str = '筛选'
      }
      return str
    },
    searchPanelLabel () {
      let str = ''
      if (this.userIdentity === '2') {
        str = '搜索子公司名称：'
      } else if (this.userIdentity === '3') {
        str = '搜索店铺名称：'
      }
      return str
    },
    searchList () {
      let list = []
      if (this.searchValue.trim() === '') {
        list = this.orgList
      } else {
        for (let item of this.orgList) {
          if (item.name.indexOf(this.searchValue) > -1) {
            list.push(item)
          }
        }
      }
      return list
    },
    selectedMultiShops () {
      return this.$store.state.selectedMultiShops
    }
  },
  methods: {
    selectedOneYear (item, index) {
      this.activeYearIndex = index
      this.selectedYear = item.name
    },
    selectedOneMonth (item, index) {
      this.activeMonthIndex = index
      this.selectedMonth = item.name
    },
    selectedOneDay (item, index) {
      if (item.name !== '占位') {
        this.activeDayIndex = index
        this.selectedDay = item.name
      }
    },
    selectedUserType (item, index) {
      if (item.name !== '占位') {
        this.activeUserTypeIndex = index
      }
    },
    hideMenu () {
      if (this.isShowSearchPanel) {
        this.selectedOrgName = this.selectedOrgNameInit
        this.isShowDatePanel = true
        this.isShowSearchPanel = false
      } else if (this.isShowSelectShopPanel) {
        this.isShowDatePanel = true
        this.isShowSelectShopPanel = false
      } else {
        this.$store.commit('updateShowLeft', false)
        this.$store.commit('updateShowMask', false)
      }
    },
    comfirmBtn () {
      if (this.isShowSearchPanel) {
        // 当在搜索页点了确定
        this.selectedOrgNameInit = this.selectedOrgName
        this.isShowDatePanel = true
        this.isShowSearchPanel = false
      } else if (this.isShowSelectShopPanel) {
        this.isShowDatePanel = true
        this.isShowSelectShopPanel = false
      } else {
        // 当在筛选页点了确定
        const y = this.selectedYear
        const m = (this.activeMonthIndex + 1) + ''
        const d = (this.activeDayIndex + 1) + ''
        const dateStr = y + m.padStart(2, '0') + d.padStart(2, '0')
        this.$store.commit('updateDateStr', dateStr)
        if (this.userIdentity === '2' || this.userIdentity === '1') {
          this.$store.commit('updateSelectedDwName', this.selectedOrgNameInit)
        } else {
          this.$store.commit('updateSelectedDpName', this.selectedOrgNameInit)
        }
        this.$store.commit('updateShowLeft', false)
        this.$store.commit('updateShowMask', false)
        if (this.activeUserTypeIndex === 1) {
          // 说明用户已经做过身份切换，现在点确定只是为了筛选数据
          if (this.userIdentityInit !== this.userIdentity) {

          } else {
            // 说明用户想要进行身份切换
            // 说明想要切换身份
            if (this.userIdentity === '1') {
              // 如果是总监，就转化为子公司
              this.$store.commit('updateUserIdentity', '2')
            } else if (this.userIdentity === '2') {
              // 如果是子公司，转化为店铺
              this.$store.commit('updateUserIdentity', '3')
            }
            // 标识他已经做了身份切换
            this.$store.commit('updateIsChangeUserType', true)
            // 跳转到首页进行新的路由分配
            this.$router.push({path: '/'})
            this.$eventBus.$emit('resetActiveMenuIndex')
            return
          }
        } else {
          // 如果用户选择了否，比较
          if (this.userIdentityInit !== this.userIdentity) {
            // 说明用户已经切换了身份，想要还原回去
            this.$store.commit('updateIsChangeUserType', false)
            this.$store.commit('updateUserIdentity', this.userIdentityInit)
            // 跳转到首页进行新的路由分配
            this.$router.push({path: '/'})
            this.$eventBus.$emit('resetActiveMenuIndex')
            return
          } else {
            // 说明用户没有进行切换操作，只是筛选了时间，什么都不用处理
          }
        }
        if (this.isShopHomePage) {
          const list = this.selectedShopsList.filter(item => {
            return item.selected
          })
          if (list.length > 0) {
            this.$store.commit('updateSelectedDpName', list[0].name)
          } else {
            this.$store.commit('updateSelectedDpName', '')
          }
          this.$store.commit('updateSelectedMultiShops', list)
        }
        // 通知其他页面，要重新查询数据了
        this.$eventBus.$emit('userSelected')
      }
    },
    selectedOneOrg (item, index) {
      this.activeOrgIndex = index
      this.selectedOrgName = item.name
    },
    showSearchPanel () {
      this.isShowDatePanel = false
      this.isShowSearchPanel = true
    },
    showSelectShopPanel () {
      this.isShowDatePanel = false
      this.isShowSelectShopPanel = true
    },
    selectedOneShop (item, index) {
      // 当点击选择或者取消选择某个店铺
      item.selected = !item.selected
      this.isSelectedAllShops()
    },
    isHomeShopPage () {
      if (this.$route.path === '/home_shop') {
        this.isShopHomePage = true
        const list = []
        for (let item of this.orgList) {
          const obj = {}
          obj.name = item.name
          obj.selected = false
          for (let one of this.selectedMultiShops) {
            if (item.name === one.name) {
              obj.selected = true
            }
          }
          list.push(obj)
        }
        this.selectedShopsList = list
      } else {
        this.isShopHomePage = false
      }
    },
    selectAllShops (index) {
      this.allShopSelected = index
      const list = []
      for (let item of this.orgList) {
        const obj = {}
        obj.name = item.name
        if (index === 1) {
          obj.selected = true
        } else if (index === 0) {
          obj.selected = false
        }
        list.push(obj)
      }
      this.selectedShopsList = list
    },
    isSelectedAllShops () {
      const list = this.selectedShopsList.filter(item => {
        return item.selected
      })
      const list2 = this.selectedShopsList.filter(item => {
        return !item.selected
      })
      if (list.length === this.orgList.length) {
        this.allShopSelected = 1
      } else if (list2.length === this.orgList.length) {
        this.allShopSelected = 0
      } else {
        this.allShopSelected = 2
      }
    }
  },
  mounted () {
    if (this.userIdentity === '2' || this.userIdentity === '1') {
      this.selectedOrgName = this.selectedDwName
    } else if (this.userIdentity === '3') {
      this.selectedOrgName = this.selectedDpName
    }
  },
  watch: {
    showLeft: {
      handler (newData, oldData) {
        if (newData) {
          this.isShowDatePanel = true
          this.isShowSearchPanel = false
          this.isShowSelectShopPanel = false
          const y = parseInt(this.dateStr.substring(0, 4))
          const m = parseInt(this.dateStr.substring(4, 6))
          const d = parseInt(this.dateStr.substring(6, 8))
          const nowDate = new Date()
          const year = nowDate.getFullYear()
          this.activeYearIndex = year - y
          this.activeMonthIndex = m - 1
          this.activeDayIndex = d - 1
          if (this.isChangeUserType) {
            this.activeUserTypeIndex = 1
          } else {
            this.activeUserTypeIndex = 0
          }
        }
        if (this.userIdentity === '2' || this.userIdentity === '1') {
          this.selectedOrgNameInit = this.selectedDwName
        } else if (this.userIdentity === '3') {
          this.selectedOrgNameInit = this.selectedDpName
        }
        this.isHomeShopPage()
        this.isSelectedAllShops()
      }
    },
    isShowSearchPanel: {
      handler (newData, oldData) {
        if (newData) {
          this.searchValue = ''
          for (let i = 0; i < this.orgList.length; i++) {
            if (this.selectedOrgName === this.orgList[i].name) {
              this.activeOrgIndex = i
              break
            }
          }
        }
      }
    }
  }
}
</script>

