      }
      .btnList-content-dqdw {
        height: px2rem(100);
        max-height: px2rem(100);
        width: 100%;
        display: flex;
        flex-direction: column;
        align-items: center;
        overflow-y: auto;
        .item-content-big {
          border: px2rem(1) solid #34C9F8;
          position: relative;
          margin: px2rem(10) auto;
          height: px2rem(50);
          line-height:  px2rem(50);
          width: 90%;
          text-align:center;
          .item-name-big {
            font-size: px2rem(24);
            color: #34C9F8;
          }
          .triangle-bottom-big {
            width:0;
            height: 0;
            display: inline-block;
            position: absolute;
            bottom: 0;
            right: 0;
            border-bottom: px2rem(20) solid #34C9F8;
            border-left: px2rem(20) solid transparent;
          }
        }
      }
      .selected-shops-btn {
        width: 90%;
        line-height: px2rem(60);
        margin: px2rem(10) auto;
        text-align: center;
        border: px2rem(1) solid #34C9F8;
      }
    }
    .select-panel-content-search {
      position: relative;
      flex: 1;
      margin-bottom: px2rem(30);
      width: 100%;
      overflow-y: auto;
      animation: btnBox 300ms;
      display: flex;
      flex-direction: column;
      .label-content {
        width: 90%;
        margin-left: px2rem(16);
        height: px2rem(60);
        line-height: px2rem(60);
        vertical-align: middle;
        font-size: px2rem(24);
        display: flex;
        justify-content: space-between;
        .search-btn {
          width: px2rem(120);
          border: px2rem(1) solid #ccc;
          text-align: center;
        }
      }
      .btnList-content-searchdw {
        margin-top: px2rem(30);
        flex: 1;
        width: 100%;
        display: flex;
        flex-direction: column;
        align-items: center;
        overflow-y: auto;
        .item-content-big {
          border: px2rem(1) solid #34C9F8;
          position: relative;
          margin: px2rem(10) auto;
          height: px2rem(50);
          line-height:  px2rem(50);
          width: 90%;
          text-align:center;
          .item-name-big {
            font-size: px2rem(24);
            color: #34C9F8;
          }
          .triangle-bottom-big {
            width:0;
            height: 0;
            display: inline-block;
            position: absolute;
            bottom: 0;
            right: 0;
