<template>
  <div class="page-header">
    <p class="title">{{headerTitle}}</p>
    <div class="chooseIcon" v-if="showIcon" @click.stop="showLeftPop">
      筛选
      <img :src="backIcon"  />
    </div>
  </div>
</template>

<script>
export default {
  name: 'PageHeader',
  data () {
    return {
      backIcon: './images/xuan@2x.png'
    }
  },
  methods: {
    showLeftPop () {
      this.$store.commit('updateShowLeft', true)
      this.$store.commit('updateShowMask', true)
    }
  },
  computed: {
    showIcon () {
      return this.$store.state.showIcon
    },
    headerTitle () {
      return this.$store.state.headerTitle
    }
  },
  watch: {}
}
</script>

<style lang="scss" scoped>
.page-header {
  color: #1B1F31;
  text-align: center;
  line-height: px2rem(88);
  .title {
    font-size: px2rem(36);
  }
  .chooseIcon {
    position: absolute;
    z-index: 999;
    right: 0;
    top: 0;
    height: px2rem(88);
    line-height: px2rem(88);
    text-align: center;
    vertical-align: middle;
    font-size: px2rem(32);
    margin-right: px2rem(20);
    font-family: 'PingFangSC-Regular';
    img {
      width: px2rem(28);
      height: px2rem(28);
    }
  }
}
</style>
