import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import apiService from './assets/js/api'
/**
 * 引入样式
 */
import 'normalize.css' // 样式重置
import './assets/style/global.scss' // 全局样式
/**
 * 引入脚本
 */
// eslint-disable-next-line
import core from './assets/js/core' // 框架基础功能
// import VConsole from 'vconsole/dist/vconsole.min.js'
import {Search} from 'vant'
Vue.use(Search)
Vue.config.productionTip = false
const errorHandler = (error, vm) => {
  console.error('抛出全局异常')
  console.error(vm)
  console.error(error)
}
Vue.config.errorHandler = errorHandler
Vue.prototype.$throw = (error) => errorHandler(error, this)
Vue.prototype.$eventBus = new Vue()
/**
 * vue实例生成前的步骤
 * 1.app.getIdentifyingCode() 去接口平台拿时间戳，
 * 2. 拿到时间戳以后，从地址url里获取用户的信息，如用户的id
 * 3. 拿到用户的信息以后，发给接口，验证用户的身份信息getUserId()
 * 4.用户身份验证完成后，获取数据库里的最新数据时间，拿到时间后，加载页面，后面页面里利用初始时间，查询相应的页面数据
 **/
window.userIdentity = '' // 1标识总监， 2标识子公司， 3标识店铺， 这个用于记录用户现在使用的身份，方便切换
window.userAccount = ''
window.userName = ''
window.authList = []
let timer = 0
const initApp = () => {
  new Vue({
    router,
    store,
    render: h => h(App)
  }).$mount('#app')
}
// 通过url获取微信入口验证用户id
async function getUserId () {
  // let url = (window.location.href).split('?') // 正式
  // let userId = url[1]
  // let userId = 'MTgwMzAxNDA=#/' // 'dXNlcmlkID0xODEyMDA1Ng==#/' // 测试 总监
  let userId = 'MTYxMDA4Nzk=#/' // 'dXNlcmlkID0xODEyMDA1Ng==#/' // 测试 子公司经理
  // let userId = 'MTgxMjAwNTE=#/' // 'dXNlcmlkID0xODEyMDA1Ng==#/' // 测试 店长
  // let userId = 'MTgxMjAwNTE=#/' // 测试账号
  console.log('加密的用户id' + userId)
  // 把考核主体和用户信息发后后台，判断用户在该主体下的身份信息
  const {__statusCode, data} = await apiService.getDataFromInterface(apiService.getUserId,
    {encodeParam: userId, examSub: '直营'})
  if (__statusCode === '1') {
    const result = JSON.parse(data)
    if (result.res_user.length > 0) {
      // 这里获取解密后的用户账号，用于埋点，
      const userAccount = result.res_user[0].userAccount
      if (userAccount !== '') {
        window.userAccount = result.res_user[0].userAccount
        window.userName = result.res_user[0].userName
        if (result.res_user[0].userIdentity === '总监') {
          window.userIdentity = '1'
        } else if (result.res_user[0].userIdentity === '子公司经理') {
          window.userIdentity = '2'
        } else if (result.res_user[0].userIdentity === '店长') {
          window.userIdentity = '3'
        }
        window.authList = result.res_user[0].role_list
        app.getNewTimeData()
        if (timer) {
          window.clearInterval(this.timer)
        }
        timer = window.setInterval(app.getIdentifyingCode, 8 * 60 * 1000)
      } else {
        alert('获取用户信息失败，请重新打开或联系管理员~')
      }
    } else {
      alert('获取用户信息失败，请重新打开或联系管理员~')
    }
  } else {
    console.error('获取UserId验证失败!')
  }
}
// 连接接口平台获取平台认证
const app = {
  async getIdentifyingCode () {
    const { __statusCode, data } = await apiService.getDataFromInterface(apiService.getIdentifyingCode)
    if (Object.is(__statusCode, '1')) {
      // 就把数据放到localStorage中，方便使用
      window.localStorage.setItem('IdentifyingCode', data.IdentifyingCode)
      window.localStorage.setItem('ExpiryTime', data.ExpiryTime)
      // 获取接口平台信息后，查询用户的信息
      if (window.userAccount === '') {
        getUserId()
      }
    }
  },
  // 获取数据库中的最新时间，用于展示
  async getNewTimeData () {
    const {__statusCode, data} = await apiService.getDataFromInterface(apiService.getNewTimeData, {})
    if (__statusCode === '1') {
      // 说明能够从数据库里获取最新的时间
      // 这里暂时先写个假时间
      // const date = '20190717'
      initApp()
      const result = JSON.parse(data)
      const date = result.res[0].period_wid
      store.commit('updateDateStrInit', date)
      store.commit('updateDateStr', date)
      store.commit('updateUserIdentityInit', window.userIdentity)
      store.commit('updateUserIdentity', window.userIdentity)
      // 如果是总监，要保存他的子公司列表，如果是子公司，要保存他的子公司列表和店铺列表
      store.commit('updateAuthList', window.authList)
      let dwListArr = []
      let dpListArr = []
      const dwList = []
      const dpList = []
      // 如果是总监或者子公司经理，role 标识子公司，shop标识店铺
      // 如果是店长，role标识店铺，zgs来存放子公司
      if (window.userIdentity === '1' || window.userIdentity === '2') {
        for (let item of window.authList) {
          dwListArr.push(item.role)
          dpListArr.push(item.shop)
        }
      } else {
        for (let item of window.authList) {
          dwListArr.push(item.role)
          dpListArr.push(item.role)
        }
      }
      dwListArr = [...new Set(dwListArr)]
      dpListArr = [...new Set(dpListArr)]
      dwListArr.map(item => {
        dwList.push({name: item})
      })
      dpListArr.map(item => {
        dpList.push({name: item})
      })
      store.commit('updateDwList', dwList)
      store.commit('updateDpList', dpList)
      store.commit('updateSelectedMultiShops', dpList)
      if (window.userIdentity === '3' && window.authList.length > 0) {
        store.commit('updateSelectedDwName', window.authList[0].zgs)
      }
    } else {
      alert('获取数据最新时间失败！')
    }
  }
}
app.getIdentifyingCode()
