/**
 * @description 常用工具方法集合
 */
const weapon = {}

/**
 * @description 判断当前日期是第几周
 * @param {Number} 年
 * @param {Number} 月
 * @param {Number} 日
 * @returns {Number} 周
 */
function getMonthWeek (year, month, day) {
  // 获取当月第一日是星期几
  let firstDate = new Date(year, month, 1).getDay()
  let week = Math.ceil((day + firstDate - 8) / 7)
  return week + 1
}

/**
 * 获取URL参数
 * @param {String} name
 * @returns {String} value
 */
function getUrlParam (name) {
  var reg = new RegExp('(^|&)' + name + '=([^&]*)(&|$)') // 构造一个含有目标参数的正则表达式对象
  var r = window.location.search.substr(1).match(reg) // 匹配目标参数
  if (r != null) {
    console.log('getUrlParam decodeURI(r[2]) =', decodeURI(r[2]))
    return decodeURI(r[2]) // 返回参数值，转码中文使中文参数不乱码
    // return unescape(r[2]) // 返回参数值，中文出现乱码
  }
}
function getMon (str) {
  let dateInt = ''
  if (str) {
    dateInt = parseInt(str.substring(4, 6))
  }
  return dateInt
}

function formatNumber (num, precision, separator) {
  var parts
  // 判断是否为数字
  if (!isNaN(parseFloat(num)) && isFinite(num)) {
    // 把类似 .5, 5. 之类的数据转化成0.5, 5, 为数据精度处理做准, 至于为什么
    // 不在判断中直接写 if (!isNaN(num = parseFloat(num)) && isFinite(num))
    // 是因为parseFloat有一个奇怪的精度问题, 比如 parseFloat(12312312.1234567119)
    // 的值变成了 12312312.123456713
    num = Number(num)
    // 处理小数点位数
    num = (typeof precision !== 'undefined' ? num.toFixed(precision) : num).toString()
    // 分离数字的小数部分和整数部分
    parts = num.split('.')
    // 整数部分加[separator]分隔, 借用一个著名的正则表达式
    parts[0] = parts[0].toString().replace(/(\d)(?=(\d{3})+(?!\d))/g, '$1' + (separator || ','))

    return parts.join('.')
  }
  return NaN
}
function getYesterDay () {
  const day = new Date()
  day.setTime(day.getTime() - 24 * 60 * 60 * 1000)
  const mon = (day.getMonth() + 1) + ''
  const yesterday = day.getFullYear() + mon.padStart(2, '0') + day.getDate()
  return yesterday
}
function initNum (val) {
  if (val === undefined || val === '' || isNaN(val) || isNaN(parseFloat(val))) {
    return 0
  } else {
    return parseFloat(val)
  }
}
// 排序方法，tag = true 表示升序
function compareSort (prop, tag = true) {
  return function (obj1, obj2) {
    var val1 = (obj1[prop]).replace(/,/g, '')
    var val2 = (obj2[prop]).replace(/,/g, '')
    if (!isNaN(Number(val1)) && !isNaN(Number(val2))) {
      val1 = Number(val1)
      val2 = Number(val2)
    }
    if (tag) {
      if (val1 < val2) {
        return -1
      } else if (val1 > val2) {
        return 1
      } else {
        return 0
      }
    } else {
      if (val1 > val2) {
        return -1
      } else if (val1 < val2) {
        return 1
      } else {
        return 0
      }
    }
  }
}
// 格式化日期 dateFtt('yyyyMMdd', new Date())
function dateFormatter (fmt, date) {
  let o = {
    'M+': date.getMonth() + 1, // 月份
    'd+': date.getDate(), // 日
    'h+': date.getHours(), // 小时
    'm+': date.getMinutes(), // 分
    's+': date.getSeconds(), // 秒
    'q+': Math.floor((date.getMonth() + 3) / 3), // 季度
    'S': date.getMilliseconds() // 毫秒
  }
  if (/(y+)/.test(fmt)) {
    fmt = fmt.replace(RegExp.$1, (date.getFullYear() + '').substr(4 - RegExp.$1.length))
  }
  for (let k in o) {
    if (new RegExp('(' + k + ')').test(fmt)) {
      fmt = fmt.replace(RegExp.$1, (RegExp.$1.length === 1) ? (o[k]) : (('00' + o[k]).substr(('' + o[k]).length)))
    }
  }
  return fmt
}
function getJiDuDate (str) {
  const y = str.substring(0, 4)
  const m = parseInt(str.substring(4, 6))
  let j = '1'
  if (m >= 1 && m <= 3) {
    j = '1'
  } else if (m >= 4 && m <= 6) {
    j = '2'
  } else if (m >= 7 && m <= 9) {
    j = '3'
  } else if (m >= 10 && m <= 12) {
    j = '4'
  }
  return y + j
}
// 处理为0的数据
function dealZero (val, str, flag = '-') {
  if (initNum(val) === 0) {
    return flag
  } else {
    return str
  }
}
function switchPercent (val, precision = 1, flag = '') {
  let str = (initNum(val) * 100).toFixed(precision) + flag
  return str
}
// 获取当月天数
function getMonthDayNum (str) {
  const year = parseInt(str.substring(0, 4))
  const month = parseInt(str.substring(4, 6))
  const d = new Date(year, month, 0)
  return d.getDate()
}
weapon.getUrlParam = getUrlParam
weapon.getMonthWeek = getMonthWeek
weapon.getMon = getMon
weapon.formatNumber = formatNumber
weapon.initNum = initNum
weapon.compareSort = compareSort
weapon.dateFormatter = dateFormatter
weapon.dealZero = dealZero
weapon.getJiDuDate = getJiDuDate
weapon.getYesterDay = getYesterDay
weapon.getMonthDayNum = getMonthDayNum
weapon.switchPercent = switchPercent

export {
  getUrlParam,
  getMonthWeek,
  getMon,
  formatNumber,
  initNum,
  compareSort,
  dateFormatter,
  dealZero,
  getJiDuDate,
  getYesterDay,
  getMonthDayNum,
  switchPercent
}
export default weapon
