/**
 * @description 框架核心功能
 * @author chenqy9
 */

import config from './config'
import rem from './rem'
import weapon from './weapon'
import request from './request'

const core = {
  config,
  rem,
  weapon,
  request
}

// 挂载全局功能服务
if (!window.$lib) {
  window.$lib = {
    ...rem,
    ...weapon
  }
} else {
  console.warn('$lib全局对象已存在！')
}

export {
  config,
  rem,
  weapon,
  request
}

export default core
